<!--  -->
<template>
  <div class="Audit">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="title" label="" width="180">
        <template slot-scope="scope">
          <router-link :to="`/news-manage/preview/${scope.row.id}`">{{
            scope.row.title
          }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="author" label="" width="180">
      </el-table-column>
      <el-table-column prop="category.title" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" @click="handleAudit(scope.row, 2, 1)"
            ></el-button
          >
          <el-button type="danger" plain @click="handleAudit(scope.row, 3, 0)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataSource: [],
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // /
    handleAudit(row, auditState, publishState) {
      this.dataSource = this.dataSource.filter((item) => item.id !== row.id);
      this.$axios
        .patch(`/news/${row.id}`, { auditState, publishState })
        .then(() => {
          this.$notify({
            title: "",
            message: `[/]`,
            position: "bottom-right",
            type: "success",
          });
        });
    },
    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
  },
  // - this
  created() {
    const { roleId, region, username } = JSON.parse(
      localStorage.getItem("token")
    );
    const roleObj = {
      1: "superadmin",
      2: "admin",
      3: "editor",
    };
    this.$axios.get(`/news?auditState=1&_expand=category`).then((res) => {
      const list = res.data;
      this.dataSource =
        roleObj[roleId] === "superadmin"
          ? list
          : [
              ...list.filter((item) => item.author === username),
              ...list.filter(
                (item) =>
                  item.region === region && roleObj[item.roleId] === "editor"
              ),
            ];
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
