<!--  -->
<template>
  <div class="NewsPublish">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="title" label="" width="180">
        <template slot-scope="scope">
          <router-link :to="`/news-manage/preview/${scope.row.id}`">{{
            scope.row.title
          }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="author" label="" width="180">
      </el-table-column>
      <el-table-column prop="category.title" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            :type="color"
            :plain="type === 1 ? false : true"
            @click="operation(scope.row, type)"
            >{{ button }}</el-button
          >
          <slot></slot>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  props: { type: Number },
  components: {},
  data() {
    //
    return {
      dataSource: [],
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {
    button() {
      let button;
      if (this.type === 1) {
        button = "";
      } else if (this.type === 2) {
        button = "";
      } else {
        button = "";
      }
      return button;
    },
    color() {
      let color;
      if (this.type === 1) {
        color = "primary";
      } else if (this.type === 2) {
        color = "danger";
      } else {
        color = "danger";
      }
      return color;
    },
  },
  //data
  watch: {},
  //
  methods: {
    // 
    operation(row, type) {
      if (type === 1) {
        this.dataSource = this.dataSource.filter((item) => item.id !== row.id);
        this.$axios
          .patch(`/news/${row.id}`, {
            publishState: 2,
            publishTime: Date.now(),
          })
          .then(() => {
            this.$notify({
              title: "",
              message: `/`,
              position: "bottom-right",
              type: "success",
            });
          });
      } else if (type === 2) {
        this.dataSource = this.dataSource.filter((item) => item.id !== row.id);
        this.$axios
          .patch(`/news/${row.id}`, {
            publishState: 3,
          })
          .then(() => {
            this.$notify({
              title: "",
              message: `/`,
              position: "bottom-right",
              type: "success",
            });
          });
      } else if (type === 3) {
        this.dataSource = this.dataSource.filter((item) => item.id !== row.id);
        this.$axios.delete(`/news/${row.id}`).then(() => {
          this.$notify({
            title: "",
            message: ``,
            position: "bottom-right",
            type: "success",
          });
        });
      }
    },
    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
  },
  // - this
  created() {
    const { username } = JSON.parse(localStorage.getItem("token"));
    this.$axios
      .get(
        `/news?author=${username}&publishState=${this.type}&_expand=category`
      )
      .then((res) => {
        this.dataSource = res.data;
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
