<!--  -->
<template>
  <div class="AuditList">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="title" label="" width="180">
        <template slot-scope="scope">
          <router-link :to="`/news-manage/preview/${scope.row.id}`">{{
            scope.row.title
          }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="author" label="" width="180">
      </el-table-column>
      <el-table-column prop="category.title" label="">
      </el-table-column>
      <el-table-column prop="auditState" label="">
        <template slot-scope="scope">
          <el-tag :type="colorList[scope.row.auditState]">{{
            auditList[scope.row.auditState]
          }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            plain
            v-show="scope.row.auditState == 1"
            @click="handleRervert(scope.row)"
            ></el-button
          >
          <el-button
            type="success"
            plain
            v-show="scope.row.auditState == 2"
            @click="handlePublish(scope.row)"
            ></el-button
          >
          <el-button
            type="primary"
            plain
            v-show="scope.row.auditState == 3"
            @click="handleUpdate(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataSource: [],
      colorList: ["", "warning", "success", "danger"],
      auditList: ["", "", "", ""],
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
    // 
    handleRervert(row) {
      this.dataSource = this.dataSource.filter((item) => item.id !== row.id);
      this.$axios
        .patch(`/news/${row.id}`, {
          auditState: 0,
        })
        .then(() => {
          this.$notify({
            title: "",
            message: ``,
            position: "bottom-right",
            type: "success",
          });
        });
    },
    // 
    handlePublish(row) {
      this.$axios
        .patch(`/news/${row.id}`, {
          publishState: 2,
          publishTime: Date.now(),
        })
        .then(() => {
          this.$router.push("/publish-manage/published");
          this.$notify({
            title: "",
            message: `/`,
            position: "bottom-right",
            type: "success",
          });
          this.$store.commit("SetActive", "/publish-manage/published");
        });
    },
    // 
    handleUpdate(row) {
      this.$router.push(`/news-manage/update/${row.id}`);
      this.$store.commit("SetActive", `/news-manage/update/${row.id}`);
    },
  },
  // - this
  created() {
    const { username } = JSON.parse(localStorage.getItem("token"));
    this.$axios
      .get(
        `/news?author=${username}&auditState_ne=0&publishState_lte=1&_expand=category`
      )
      .then((res) => {
        this.dataSource = res.data;
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
.el-button + .el-button {
  margin-left: 0px;
}
</style>
