<!--  -->
<template>
  <div class="detail">
    <a-page-header
      style=""
      v-if="newsInfo.title && newsInfo.category.title"
      :title="newsInfo.title"
      :sub-title="newsInfo.category.title"
      @back="back"
    />
    <a-descriptions size="small" style="padding: 0 24px">
      <a-descriptions-item label=""
        >{{ newsInfo.author }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ time }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.region }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.view }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.star }}
        <i
          class="el-icon-thumb"
          style="color: red; margin-left: 20px"
          @click="handleStar"
        ></i>
      </a-descriptions-item>
      <a-descriptions-item label=""> 0 </a-descriptions-item>
    </a-descriptions>
    <div
      style="margin: 0 24px; border: 1px solid gray"
      v-html="newsInfo.content"
    ></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  name: "Detail",
  components: {},
  data() {
    //
    return {
      newsInfo: [],
    };
  },
  // data
  computed: {
    time() {
      return this.$day(this.newsInfo.publishTime).format("YYYY/MM/DD HH:mm:ss");
    },
  },
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    },
    handleStar() {
      // this.newsInfo = {
      //   ...this.newsInfo,
      //   star: this.newsInfo.star + 1,
      // };
      this.newsInfo.star++;
      console.log(this.newsInfo.star);
      this.$axios.patch(`/news/${this.$route.params.id}`, {
        star: this.newsInfo.star,
      });
    },
  },
  // - this
  created() {
    this.$axios
      .get(`/news/${this.$route.params.id}?_expand=category&_expand=role`)
      .then((res) => {
        this.newsInfo = { ...res.data, view: res.data.view + 1 };
        return res.data;
      })
      .then((res) => {
        this.$axios.patch(`/news/${this.$route.params.id}`, {
          view: res.view + 1,
        });
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
