<!--  -->
<template>
  <div class="topHeader">
    <i
      class="el-icon-s-unfold fold"
      v-show="isCollapse == true"
      @click="OPEN"
    ></i>
    <i
      class="el-icon-s-fold fold"
      v-show="isCollapse == false"
      @click="OPEN"
    ></i>
    <div class="right">
      <span
        > <span style="color: #1890ff">{{ username }}</span> </span
      >
      <div class="headPortrait">
        <el-dropdown :hide-on-click="false" placement="bottom">
          <el-avatar :size="size" :src="circleUrl"></el-avatar>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>{{ roleName }}</el-dropdown-item>
            <el-dropdown-item @click.native="withdraw"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import { mapMutations, mapState } from "vuex";
// const {role:{roleName},username} = JSON.parse(localStorage.getItem('token'))
export default {
  name: "TopHeader",
  //import
  components: {},
  data() {
    //
    return {
      circleUrl: "https://s1.ax1x.com/2022/06/13/XfE6xI.jpg",
      size: 30,
    };
  },
  // data
  computed: {
    ...mapState(["isCollapse"]),
    username() {
      return JSON.parse(localStorage.getItem("token")).username;
    },
    roleName() {
      return JSON.parse(localStorage.getItem("token")).role.roleName;
    },
  },
  //data
  watch: {},
  //
  methods: {
    withdraw() {
      localStorage.removeItem("token");
      this.$store.commit("ClearActive");
      this.$store.commit("ClearIsCollapse");
      this.$router.push("/login");
    },
    ...mapMutations(["OPEN"]),
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
.topHeader {
  padding: 0 16px;
}
.fold {
  margin-top: 20px;
}
.right {
  float: right;
  line-height: 60px;
}
.el-dropdown {
  height: 30px;
}
.headPortrait {
  float: right;
  padding-top: 10px;
  height: 30px;
}
</style>
