<!--  -->
<template>
  <div class="news">
    <div class="header">
      <h3></h3>
      <span></span>
    </div>
    <el-row :gutter="32">
      <el-col :span="8" v-for="item in list" :key="item.id" class="box">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>{{ item[0] }}</span>
          </div>
          <a-list
            item-layout="vertical"
            size="large"
            :pagination="pagination"
            :data-source="item[1]"
          >
            <a-list-item slot="renderItem" key="item.title" slot-scope="item">
              <a-list-item-meta :description="item.description">
                <!-- <a slot="title" :href="`/detail/${item.id}`">{{
                  item.title
                }}</a> -->
                <router-link slot="title" :to="`/detail/${item.id}`">
                  {{ item.title }}</router-link
                >
              </a-list-item-meta>
            </a-list-item>
          </a-list>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  name: "News",
  //import

  components: {},
  data() {
    //
    return {
      list: [],
      pagination: {
        pageSize: 3,
      },
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
  },
  // - this
  created() {
    this.$axios.get(`/news?publishState=2&_expand=category`).then((res) => {
      // console.log(res.data);

      const nList = Object.entries(
        this._.groupBy(res.data, (item) => item.category.title)
      );
      // console.log(nList);
      this.list = nList;
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>

<style scoped>
.news {
  width: 95%;
  margin: 0 auto;
}
.ant-list-item-meta-title > a {
  color: rgb(25 68 217 / 65%);
  transition: all 0.3s;
}
.el-col {
  border-radius: 4px;
  height: 320px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.header {
  margin-bottom: 50px;
  margin-top: 50px;
  overflow: hidden;
  line-height: 30px;
  padding: 0 30px;
}
.header h3,
.header span {
  float: left;
  font-size: 26px;
}
.header span {
  margin-left: 20px;
  font-size: 16px;
}
.box-card {
  margin-bottom: 20px;
}
.box {
  float: left;
}
.text {
  padding: 10px;
  border-bottom: 1px solid rgb(242, 234, 234);
}
.ant-list-item,
.ant-list-item-no-flex {
  margin: 0 !important;
  padding: 0 !important;
}
.ant-list-vertical .ant-list-item-meta {
  margin-bottom: 0px;
  /* margin: 10px 0 !important; */
  margin-top: 10px;
}
</style>
