<!--  -->
<template>
  <div class="newsDraft">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="id" label="ID" width="150"> </el-table-column>
      <el-table-column prop="title" label="">
        <template slot-scope="scope">
          <router-link :to="`/news-manage/preview/${scope.row.id}`">{{
            scope.row.title
          }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="author" label=""> </el-table-column>
      <el-table-column prop="category.title" label=""> </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            circle
          ></el-button>
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            slot="reference"
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            type="primary"
            icon="el-icon-upload"
            circle
            slot="reference"
            @click="handleCheck(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataSource: [],
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // 
    handleDelete(row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.dataSource = this.dataSource.filter(
            (data) => data.id !== row.id
          );
          this.$axios.delete(`/news/${row.id}`);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    handleEdit(row) {
      this.$router.push(`/news-manage/update/${row.id}`);
      this.$store.commit("SetActive", `/news-manage/update/${row.id}`);
    },
    // 
    handleCheck(row) {
      this.$axios
        .patch(`/news/${row.id}`, {
          auditState: 1,
        })
        .then(() => {
          this.$router.push("/audit-manage/list");
          this.$notify({
            title: "",
            message: `${""}`,
            position: "bottom-right",
            type: "success",
          });
          this.$store.commit("SetActive", "/audit-manage/list");
        });
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
    },
  },
  // - this
  created() {
    const { username } = JSON.parse(localStorage.getItem("token"));
    this.$axios
      .get(`/news?author=${username}&auditState=0&_expand=category`)
      .then((res) => {
        this.dataSource = res.data;
        console.log(this.dataSource);
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
