<!--  -->
<template>
  <div class="UserForm">
    <el-form
      :model="ruleForm"
      :rules="rules"
      size="small"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
      label-position="top"
    >
      <el-form-item label="" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="region">
        <el-select v-model="ruleForm.region" :disabled="isDisabled">
          <el-option
            v-for="item in regionList"
            :key="item.id"
            :label="item.title"
            :value="item.value"
            :disabled="checkRegionDisabled(item)"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="roleId">
        <el-select v-model="ruleForm.roleId" @change="refresh">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.roleType"
            :disabled="checkRoleDisabled(item)"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: ["roleList", "regionList", "ruleForm"],
  data() {
    //
    return {
      // disabled: this.isDisabled, //
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 12,
            message: " 1  12 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 12,
            message: " 3  12 ",
            trigger: "blur",
          },
        ],
        region: this.disabled
          ? []
          : [{ required: true, message: "", trigger: "blur" }],
        roleId: [{ required: true, message: "", trigger: "blur" }],
      },
      roleObj: {
        1: "superadmin",
        2: "admin",
        3: "editor",
      },
      isDisabled: false, //
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // 
    checkRegionDisabled(item) {
      const { roleId, region } = JSON.parse(localStorage.getItem("token"));
      if (this.roleObj[roleId] === "superadmin") {
        return false;
      } else {
        return item.value !== region;
      }
    },
    // 
    checkRoleDisabled(item) {
      const { roleId } = JSON.parse(localStorage.getItem("token"));
      if (this.roleObj[roleId] === "superadmin") {
        return false;
      } else {
        return this.roleObj[item.id] !== "editor";
      }
    },
    refresh() {
      if (this.ruleForm.roleId === 1) {
        this.isDisabled = true;
        this.ruleForm.region = "";
      } else {
        this.isDisabled = false;
      }
    },
  },
  // - this
  created() {
    this.refresh();
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {
    this.refresh();
  }, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
.el-select {
  width: 100%;
}
</style>
