<!--  -->
<template>
  <div class="detail">
    <a-page-header
      style=""
      v-if="newsInfo.title && newsInfo.category.title"
      :title="newsInfo.title"
      :sub-title="newsInfo.category.title"
      @back="back"
    />
    <a-descriptions size="small" style="padding: 0 24px">
      <a-descriptions-item label=""
        >{{ newsInfo.author }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ time }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ _ }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.region }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        <el-tag :type="colorList[newsInfo.auditState]">{{
          auditList[newsInfo.auditState]
        }}</el-tag>
      </a-descriptions-item>
      <a-descriptions-item label="">
        <span>{{ publishList[newsInfo.publishState] }}</span>
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.view }}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{ newsInfo.star }}
      </a-descriptions-item>
      <a-descriptions-item label=""> 0 </a-descriptions-item>
    </a-descriptions>
    <div
      style="margin: 0 24px; border: 1px solid gray"
      v-html="newsInfo.content"
    ></div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  name: "Detail",
  components: {},
  data() {
    //
    return {
      newsInfo: [],
      publishList: ["", "", "", ""],
      auditList: ["", "", "", ""],
      colorList: ["info", "warning", "success", "danger"],
    };
  },
  // data
  computed: {
    time() {
      return this.$day(this.newsInfo.publishTime).format("YYYY/MM/DD HH:mm:ss");
    },
  },
  //data
  watch: {},
  //
  methods: {
    back() {
      this.$router.go(-1);
    },
  },
  // - this
  created() {
    this.$axios
      .get(`/news/${this.$route.params.id}?_expand=category&_expand=role`)
      .then((res) => {
        this.newsInfo = res.data;
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
