<!--  -->
<template>
  <div class="roleList">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="id" label="ID" width="300"> </el-table-column>
      <el-table-column prop="roleName" label="" width="300">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="scope.row.default"
            @click="handleDelete(scope.row)"
            circle
          ></el-button>
          <el-button
            type="primary"
            icon="el-icon-more"
            :disabled="scope.row.default"
            circle
            @click="handleEdit(scope.row, rightList)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-tree
        :props="props"
        :data="rightList"
        show-checkbox
        accordion
        check-strictly
        node-key="key"
        ref="tree"
        :default-checked-keys="currentRights"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleCancel"> </el-button>
        <el-button type="primary" @click="handleOk"> </el-button>
      </span>
    </el-dialog>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dialogVisible: false,
      dataSource: [],
      rightList: [],
      props: {
        label: "title",
        children: "children",
      },
      currentRights: [],
      currentId: 0,
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {
    tableData() {
      return this.dataSource;
    },
  },
  //data
  watch: {},
  //
  methods: {
    // 
    handleDelete(row) {
      console.log(row);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        this.$message({
          type: "success",
          message: "!",
        });
        this.$axios.delete(`/roles/${row.id}`);
        this.dataSource = this.dataSource.filter((item) => {
          return item.id !== row.id;
        });
      });
    },
    // 
    handleEdit(row) {
      this.dialogVisible = true;
      this.currentId = row.id;
      this.currentRights = row.rights;
      // console.log(row.rights);
    },
    // handleCheckChange(data, checked, indeterminate) {
    //   console.log(data, checked, indeterminate);
    // },
    // 
    handleOk() {
      this.dialogVisible = false;
      this.resetChecked();
    },
    // 
    handleCancel() {
      this.dialogVisible = false;
    },
    // 
    resetChecked() {
      // key
      this.currentRights = this.$refs.tree.getCheckedKeys(false);

      this.$axios.patch(`/roles/${this.currentId}`, {
        rights: this.currentRights,
      });
      // key
      this.currentRights = [];
      // key
      this.$refs.tree.setCheckedKeys([]);
      // key
      setTimeout(() => {
        this.getDataSource();
      }, 10);
    },
    getDataSource() {
      this.$axios.get("/roles").then((res) => {
        this.dataSource = res.data;
      });
    },
    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
  },
  // - this
  created() {
    this.getDataSource();
    this.$axios.get("/rights?_embed=children").then((res) => {
      this.rightList = res.data;
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
