<!--  -->
<template>
  <div class="login">
    <vue-particles color="#dedede"></vue-particles>
    <div class="formContainer">
      <h3 class="lpogintitle"></h3>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="ruleForm.name"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="ruleForm.password"
            placeholder=""
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            ></el-button
          >
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  name: "Login",
  components: {},
  data() {
    //
    return {
      ruleForm: {
        name: "",
        password: "",
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 12,
            message: " 1  12 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 12,
            message: " 3  12 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .get(
              `/users?username=${this.ruleForm.name}&password=${this.ruleForm.password}&roleState=true&_expand=role`
            )
            .then((res) => {
              // console.log(res.data);
              if (res.data.length === 0) {
                this.$message.error("");
              } else {
                localStorage.setItem("token", JSON.stringify(res.data[0]));
                this.$router.push("/home");
              }
            });
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
#particles-js {
  width: 100%;
  background: rgb(35, 39, 65);
  height: calc(100% - 5px);
  position: absolute;
}
.formContainer {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.1);
  width: 500px;
  height: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
}
.lpogintitle {
  text-align: center;
  height: 80px;
  line-height: 80px;
  font-size: 30px;
  color: white;
}
</style>
