<!--  -->
<template>
  <div class="rightList">
    <el-table
      :data="
        dataSource.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      indent.num="20"
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="ID" width="180"> </el-table-column>
      <el-table-column prop="title" label="" width="180">
      </el-table-column>
      <el-table-column prop="key" label=""> </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="scope.row.default"
            @click="handleDelete(scope.row)"
            circle
          ></el-button>
          <el-popover placement="top" width="80" trigger="click">
            <div style="text-align: center; margin: 0">
              <p></p>
              <el-switch
                :value="scope.row.pagepermisson === 1 ? true : false"
                @change="handleEdit(scope.row, scope.row.pagepermisson)"
              >
              </el-switch>
            </div>
            <el-button
              type="primary"
              icon="el-icon-s-operation"
              :disabled="scope.row.pagepermisson === undefined"
              circle
              slot="reference"
            ></el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="dataSource.length"
    >
    </el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataSource: [],
      currentPage: 1, // 
      pageSize: 5, // 
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // 
    handleDelete(row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        // console.log(row);
        if (row.grade === 1) {
          this.dataSource = this.dataSource.filter(
            (item) => item.id !== row.id
          );
          this.$axios.delete(`/rights/${row.id}`);
        } else {
          let list = this.dataSource.filter((item) => item.id === row.rightId);
          list[0].children = list[0].children.filter(
            (item) => item.id !== row.id
          );
          this.dataSource = [...this.dataSource];
          this.$axios.delete(`/children/${row.id}`);
        }
        this.$message({
          type: "success",
          message: "!",
        });
      });
    },
    handleEdit(row) {
      row.pagepermisson = row.pagepermisson === 1 ? 0 : 1;
      // console.log(this.dataSource);
      this.dataSource = [...this.dataSource];
      if (row.grade === 1) {
        this.$axios.patch(`/rights/${row.id}`, {
          pagepermisson: row.pagepermisson,
        });
      } else {
        this.$axios.patch(`/children/${row.id}`, {
          pagepermisson: row.pagepermisson,
        });
      }
    },
    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
  },
  // - this
  created() {
    this.$axios.get("/rights?_embed=children").then((res) => {
      this.dataSource = res.data;
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
