<!--  -->
<template>
  <div class="newsAdd">
    <a-page-header style="" title="" subTitle="This is a subtitle" />
    <el-steps :active="current" finish-status="success" align-center>
      <el-step title="" description=""></el-step>
      <el-step title="" description=""></el-step>
      <el-step title="" description=""></el-step>
    </el-steps>
    <div style="margin-top: 40px" v-show="current === 0">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="150px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="title">
          <el-input
            v-model="ruleForm.title"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="categoryId">
          <el-select
            v-model="ruleForm.categoryId"
            placeholder=""
            size="medium"
          >
            <el-option
              v-for="item in categoryList"
              :key="item.id"
              :label="item.title"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div style="margin-top: 40px" v-show="current === 1">
      <NewsEditor :formInline="formInline" />
    </div>

    <div>
      <span v-show="current === 2">
        <el-button
          style="margin-top: 12px"
          type="primary"
          @click="handleSave(0)"
          ></el-button
        >
        <el-button
          style="margin-top: 12px"
          type="success"
          plain
          @click="handleSave(1)"
          ></el-button
        >
      </span>
      <el-button
        style="margin-top: 12px"
        @click="handleNext"
        type="primary"
        v-show="current < 2"
        >
      </el-button>
      <el-button
        style="margin-top: 12px"
        @click="handlePrevious"
        v-show="current > 0"
        ></el-button
      >
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import NewsEditor from "../../components/news-manage/NewsEditor";
export default {
  //import
  components: { NewsEditor },
  data() {
    //
    return {
      current: 0,
      categoryList: [], //
      ruleForm: {
        title: "",
        categoryId: 0,
      },
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        categoryId: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      // content: "", //
      formInline: {
        contentFileList: "",
      },
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    // 
    handlePrevious() {
      this.current = this.current - 1;
      console.log(this.current);
    },
    // 
    handleNext() {
      if (this.current === 0) {
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
            console.log(this.ruleForm);
            this.current = this.current + 1;
          }
        });
      } else {
        // 
        if (
          this.formInline.contentFileList === "" ||
          this.formInline.contentFileList.trim() === "<p></p>"
        ) {
          this.$message.error("");
        } else {
          console.log(this.formInline.contentFileList);
          this.current = this.current + 1;
        }
      }
    },
    // 
    handleSave(auditState) {
      this.$axios
        .patch(`/news/${this.$route.params.id}`, {
          ...this.ruleForm,
          content: this.formInline.contentFileList,
          auditState: auditState,
        })
        .then(() => {
          this.$router.push(
            auditState === 0 ? "/news-manage/draft" : "/audit-manage/list"
          );
          this.$notify({
            title: "",
            message: `${
              auditState === 0 ? "" : ""
            }`,
            position: "bottom-right",
            type: "success",
          });
          this.$store.commit(
            "SetActive",
            auditState === 0 ? "/news-manage/draft" : "/audit-manage/list"
          );
        });
    },
  },
  // - this
  created() {
    this.$axios.get("/categories").then((res) => {
      this.categoryList = res.data;
    });
    this.$axios
      .get(`/news/${this.$route.params.id}?_expand=category&_expand=role`)
      .then((res) => {
        let { title, categoryId, content } = res.data;
        this.ruleForm.title = title;
        this.ruleForm.categoryId = categoryId;
        this.formInline.contentFileList = content;
        console.log(res.data);
      });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
.el-select {
  width: 100%;
}
</style>
