<!--  -->
<template>
  <div class="">
    <el-button type="primary" @click="addUser"></el-button>
    <el-table
      :data="
        userList.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      style="width: 100%"
    >
      <el-table-column prop="region" label="" width="180">
        <template slot-scope="scope">
          {{ scope.row.region !== "" ? scope.row.region : "" }}
        </template>
      </el-table-column>
      <el-table-column prop="role.roleName" label="" width="180">
      </el-table-column>
      <el-table-column prop="username" label=""> </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-switch
            :disabled="scope.row.default"
            :value="scope.row.roleState"
            @change="roleState(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="scope.row.default"
            @click="handleDelete(scope.row)"
            circle
          ></el-button>
          <el-button
            type="primary"
            icon="el-icon-edit"
            :disabled="scope.row.default"
            circle
            @click="handleEdit(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      align="right"
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      layout=" prev, pager, next"
      :total="userList.length"
    >
    </el-pagination>

    <el-dialog
      :title="title == 'add' ? '' : ''"
      :visible.sync="isAddvisible"
      width="30%"
    >
      <span>
        <UserForm
          ref="addFrom"
          :roleList="roleList"
          :regionList="regionList"
          :ruleForm="ruleForm"
        />
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="confirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import UserForm from "@/components/user-manage/UserForm";
export default {
  name: "UserList",
  //import
  components: {
    UserForm,
  },
  data() {
    //
    return {
      // radioModel: true,
      region: {},
      userList: [],
      regionList: [],
      roleList: [],
      isAddvisible: false, //
      ruleForm: {
        username: "",
        password: "",
        region: "",
        roleId: "",
      },
      // isDisabled:false,//
      currentPage: 1, // 
      pageSize: 5, // 
      title: "add",
      // timer: new Date().getTime(),
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    //
    addUser() {
      this.title = "add";
      this.isAddvisible = true;
    },
    confirm() {
      if (this.title === "add") {
        this.$refs.addFrom.$refs.ruleForm.validate((valid) => {
          if (valid) {
            this.$axios
              .post(`/users`, {
                ...this.ruleForm,
                roleState: true,
                default: false,
              })
              .then((res) => {
                this.userList = [
                  ...this.userList,
                  {
                    ...res.data,
                    role: this.roleList.filter(
                      (item) => item.id === this.ruleForm.roleId
                    )[0],
                  },
                ];
                this.ruleForm = {
                  username: "",
                  password: "",
                  region: "",
                  roleId: "",
                };
              });
            this.isAddvisible = false;
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.$refs.addFrom.$refs.ruleForm.validate((valid) => {
          if (valid) {
            this.$axios
              .patch(`/users/${this.ruleForm.id}`, {
                ...this.ruleForm,
              })
              .then(() => {
                this.getUserList();
                this.ruleForm = {
                  username: "",
                  password: "",
                  region: "",
                  roleId: "",
                };
              });
            this.isAddvisible = false;
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },

    // 
    cancel() {
      this.isAddvisible = false;
      this.ruleForm = {
        username: "",
        password: "",
        region: "",
        roleId: "",
      };
      // this.$refs.addFrom.refresh();
      this.$refs.addFrom.$refs.ruleForm.clearValidate();
    },
    // 
    roleState(row) {
      row.roleState = !row.roleState;
      this.$axios.patch(`/users/${row.id}`, {
        roleState: row.roleState,
      });
      // console.log(row);
    },
    // 
    handleEdit(row) {
      this.isAddvisible = true;
      this.title = "edit";
      this.ruleForm.username = row.username;
      this.ruleForm.password = row.password;
      this.ruleForm.region = row.region;
      this.ruleForm.roleId = row.roleId;
      this.ruleForm.roleState = row.roleState;
      this.ruleForm.default = row.default;
      this.ruleForm.id = row.id;
      // console.log(this.ruleForm);
    },
    // 
    handleDelete(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.userList = this.userList.filter((data) => data.id !== row.id);
          this.$axios.delete(`/users/${row.id}`);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },

    // 
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val;
    },
    // UserList
    getUserList() {
      const roleObj = {
        1: "superadmin",
        2: "admin",
        3: "editor",
      };
      const { roleId, region, username } = JSON.parse(
        localStorage.getItem("token")
      );
      this.$axios.get("/users?_expand=role").then((res) => {
        const list = res.data;
        this.userList =
          roleObj[roleId] === "superadmin"
            ? list
            : [
                ...list.filter((item) => item.username === username),
                ...list.filter(
                  (item) =>
                    item.region === region && roleObj[item.roleId] === "editor"
                ),
              ];
      });
    },
  },
  // - this
  created() {
    // console.log(this.timer);
    this.getUserList();
    this.$axios.get("/regions").then((res) => {
      this.regionList = res.data;
      // console.log(this.regionList);
    });
    this.$axios.get("/roles").then((res) => {
      // console.log(res.data);
      this.roleList = res.data;
      // console.log(this.roleList);
    });
  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/* @import url(); css */
</style>
